#articlesWrapper {
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  display: grid;
}

.info-card {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.info-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  flex-direction: row;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.info-item {
  margin: 0;
  padding: 5px;
  font-weight: bold;
}

.button-container {
  justify-content: center;
  align-items: center;
  margin: 5px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.load-more:hover {
  background-color: #0056b3;
}

.hidden {
  display: none;
}

